.checkmark:before {
  content: 'L';
  font-family: "gothambook", sans-serif;
  transform: scaleX(-1) rotate(-35deg);
  display: inline-block;
  vertical-align: middle;
  line-height: 1em;
  width: 1em;
  color: #000000c7;
  height: 1em;
  position: absolute;
  left: 90%;
  top: 10px;
}

/* accordian css start */
.order-tests-accordion {
  margin: 14px 0px;
  -webkit-box-shadow: -1px 1px 5px 4px rgb(209 207 209 / 50%);
  -moz-box-shadow: -1px 1px 5px 4px rgb(209 207 209 / 50%);
  box-shadow: -1px 1px 5px 4px rgb(209 207 209 / 50%);
  border-radius: 5px;
}

.order-tests-accordion .accordion-header {
  border-bottom: 2px solid #80808026;
  padding: 14px 14px 0px 14px;
}

.order-tests-accordion .accordion-header p {}

.order-tests-accordion .accordion-header p i {
  float: right;
  color: #969696;
}

.order-tests-accordion .accordion-body {
  padding: 14px;
}

::ng-deep .modal-dialog {
